<template>
  <div class="container mx-auto py-8 space-y-8">
    <!-- 主要功能区 -->
    <div class="bg-white rounded-xl shadow-sm border border-slate-200 p-6 md:p-8">
      <!-- 输入区域 -->
      <div class="space-y-6">
        <div>
          <div class="flex justify-between items-center mb-2">
            <label class="text-sm font-medium text-slate-700">{{ t('input.label') }}</label>
            <div class="flex space-x-3">
              <button
                @click="pasteToInput"
                class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/clipboard.svg"
                  alt="粘贴"
                  class="w-4 h-4 mr-1"
                />
                {{ t('buttons.paste') }}
              </button>
              <button
                @click="copyInput"
                class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                  alt="复制"
                  class="w-4 h-4 mr-1"
                />
                {{ t('buttons.copy') }}
              </button>
              <button
                @click="clearInput"
                class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/x.svg"
                  alt="清空"
                  class="w-4 h-4 mr-1"
                />
                {{ t('buttons.clear') }}
              </button>
            </div>
          </div>
          <textarea
            v-model="inputText"
            rows="6"
            class="w-full rounded-lg border border-slate-300 focus:border-primary-500 focus:ring-2 focus:ring-primary-500/20 resize-y p-3 text-slate-800 font-mono text-sm"
            :placeholder="t('input.placeholder')"
          ></textarea>
        </div>

        <!-- 转换按钮组 -->
        <div class="flex justify-center space-x-4">
          <button
            @click="encode"
            class="px-6 py-2.5 bg-gradient-to-r from-primary-600 to-primary-700 hover:from-primary-700 hover:to-primary-800 text-white font-medium rounded-lg shadow-md hover:shadow-lg transition-all flex items-center"
          >
            <img
              src="https://unpkg.com/lucide-static@latest/icons/arrow-down.svg"
              alt="编码"
              class="w-5 h-5 mr-2"
            />
            {{ t('buttons.encode') }}
          </button>
          <button
            @click="decode"
            class="px-6 py-2.5 bg-slate-600 hover:bg-slate-700 text-white font-medium rounded-lg shadow-md hover:shadow-lg transition-all flex items-center"
          >
            <img
              src="https://unpkg.com/lucide-static@latest/icons/arrow-down.svg"
              alt="解码"
              class="w-5 h-5 mr-2"
            />
            {{ t('buttons.decode') }}
          </button>
        </div>

        <!-- 输出区域 -->
        <div>
          <div class="flex justify-between items-center mb-2">
            <label class="text-sm font-medium text-slate-700">{{ t('output.label') }}</label>
            <div class="flex space-x-3">
              <button
                @click="copyOutput"
                class="text-sm text-slate-600 hover:text-primary-600 flex items-center transition-colors"
              >
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                  alt="复制"
                  class="w-4 h-4 mr-1"
                />
                {{ t('buttons.copy') }}
              </button>
            </div>
          </div>
          <textarea
            v-model="outputText"
            rows="6"
            class="w-full rounded-lg border border-slate-300 bg-slate-50/50 p-3 text-slate-800 font-mono text-sm"
            readonly
          ></textarea>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { copyToClipboard, clipboardText } from "~/utils/clipboard";
import { ref } from "vue";

const { t } = useI18n({
  useScope: "local",
});

// 文本数据
const inputText = ref("");
const outputText = ref("");

/**
 * URL 编码
 */
const encode = () => {
  try {
    outputText.value = encodeURIComponent(inputText.value);
  } catch (error) {
    console.error("编码错误:", error);
    outputText.value = t('errors.encoding');
  }
};

/**
 * URL 解码
 */
const decode = () => {
  try {
    outputText.value = decodeURIComponent(inputText.value);
  } catch (error) {
    console.error("解码错误:", error);
    outputText.value = t('errors.decoding');
  }
};

/**
 * 将剪贴板内容粘贴到输入框
 */
const pasteToInput = async () => {
  try {
    inputText.value = await clipboardText();
  } catch (error) {
    console.error("剪贴板粘贴错误:", error);
  }
};

/**
 * 复制输入框内容到剪贴板
 */
const copyInput = () => {
  copyToClipboard(inputText.value);
};

/**
 * 清空输入框
 */
const clearInput = () => {
  inputText.value = "";
};

/**
 * 复制输出结果到剪贴板
 */
const copyOutput = () => {
  copyToClipboard(outputText.value);
};
</script>

