      "paste": "Paste",
      "copy": "Copy",
      "clear": "Clear",
      "encode": "Encode",
      "decode": "Decode"
    },
    "output": {
      "label": "Result"
    },
    "errors": {
      "encoding": "Encoding error, please check input content",
      "decoding": "Decoding error, please ensure input is valid URL encoded string"
    }
  },
  "zh": {
    "name": "URL 编码/解码工具",
    "input": {
